.header {
  display: flex;
  justify-content: space-between;
  flex:1;
  padding: 12px;
  position: relative;
  align-items: center;
}

.menu {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.menu-icon {
  font-size: 24px;
}

.mobile-menu {
  display: none;
}

.web-menu {
  display: flex;
}

.logo img {
  display: flex;
  width: 4rem;

}

/* Collapse into hamburger icon, Shrink logo */
@media only screen and (max-width:720px) {
  .mobile-menu {
    display: flex;
  }

  .web-menu {
    display: none;
  }

  .logo {
    font-size: 24px;
  }
}